



















import Vue from 'vue';
export default Vue.extend({
    data() {
        return {
            activeIndex: '1',
        };
    },
    methods: {
        handleSelect(key, keyPath) {
            this.$emit('handleMenuSelect', key, keyPath);
        },
        routerName(routerName: string): void {
            this.$router.push(routerName);
        }
    }
});
